












































.el-breadcrumb__item:last-child .el-breadcrumb__inner, .el-breadcrumb__item:last-child .el-breadcrumb__inner:hover, .el-breadcrumb__item:last-child .el-breadcrumb__inner a, .el-breadcrumb__item:last-child .el-breadcrumb__inner a:hover{color:#fff;}
.el-breadcrumb__inner.is-link, .el-breadcrumb__inner a{color:#fff}
  .app-breadcrumb.el-breadcrumb {

    display: inline-block;
