@import './variables.scss';
@import './mixin.scss';
@import './transition.scss';
@import './element-ui.scss';
@import './sidebar.scss';

body {
  height: 100%;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Arial, sans-serif;
  background:#EDEDED
}
.card-box{transition: .5s; position:relative}
.card-box:hover {
    -webkit-transform: translateY(-6px);
    transform: translateY(-6px);
    -moz-box-shadow: 0 20px 20px rgba(0,0,0,.1);
    webkit-box-shadow: 0 0 25px -5px #9e9c9e;
    box-shadow: 0 0 25px -5px #9e9c9e;
}
.header{width:100%;background:#00508F;height:50px; position:fixed; z-index:100; left:0; top:0;}
label {
  font-weight: 700;
}

html {
  height: 100%;
  box-sizing: border-box;
}

#app{
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

a,
a:focus,
a:hover {
  cursor: pointer;
  color: inherit;
  outline: none;
  text-decoration: none;
}

div:focus{
  outline: none;
 }

a:focus,
a:active {
  outline: none;
}

a,
a:focus,
a:hover {
  cursor: pointer;
  color: inherit;
  text-decoration: none;
}

.clearfix {
  &:after {
    visibility: hidden;
    display: block;
    font-size: 0;
    content: " ";
    clear: both;
    height: 0;
  }
}

//main-container全局样式
.app-main{
  min-height: 100%;
}

.app-container {
  padding:0 20px;
}
.bcblue-shadow{ background:#389af0!important; }
.bg-orange-shadow{background:#E67D21!important;}
.bg-purple-shadow{background:#8E44AD!important;}
.bg-success-shadow{background:#28a745!important;}
.bg-powder-shadow{background:#ff4081!important;}

.div-warp{padding:0 20px;  overflow: hidden;}
.formWidth{width:555px;}
.el-dialog__body{padding-top:10px!important}


.block-warp{background: #fff; border-radius: 3px; padding:20px;}
.search-warp{padding:20px 0 20px 0; overflow:hidden;}
.search-warp strong{ display: inline-block;float: left; line-height: 35px; font-size:14px; font-weight: normal; }
.search-warp .inputStyle{width: 300px; display: inline-block;float:left; margin-right: 15px; }
.search-warp ul{padding:0; margin:0;}
.search-warp li{float:left; list-style:none;}